    background-color: #101112;
    color: #AED2FF;
    padding: 18px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.title {
    font-size: 30px;
}
.navbar-logo{
    text-decoration: none;
    color: #063a79;
